/*
 ** 处理顶部导航的action
 ** @param state{object} 状态
 ** @param action(string) action事件
 */
import {fromJS} from 'immutable'
import {FourPicOnClick} from './action'

const initialState = fromJS({
  FourPic:[]
});


function reducer(state=initialState ,action){
	switch (action.type) {
		case FourPicOnClick:
			console.log(`News FourPicOnClick click`)
			console.log(state)
			return state
		default:
			return state;
	}
}
export default reducer;


